import type { LineStyleProps } from '@antv/g';
import { isEmpty } from 'lodash';
import {
  CellBorderPosition,
  CellClipBox,
  type CellTheme,
  type IconTheme,
  type TextAlign,
  type TextBaseline,
} from '../../common/interface';
import type { SimpleBBox } from '../../engine';
import { getIconTotalWidth, type GroupedIcons } from './header-cell';

/**
 * text  icon 
 *    y text  icon 
 *    x
 *       1. text  icon  left  right icon bbox  left  right 
 *       2.  text width  icon bbox 
 */

export const getVerticalTextPosition = (
  bbox: SimpleBBox,
  textBaseline: TextBaseline,
) => {
  const { y, height } = bbox;

  switch (textBaseline) {
    case 'top':
      return y;
    case 'middle':
      return y + height / 2;
    default:
      return y + height;
  }
};

export const getVerticalIconPosition = (
  iconSize: number,
  textY: number,
  textFontSize: number,
  textBaseline: TextBaseline,
) => {
  const offset = (textFontSize - iconSize) / 2;

  switch (textBaseline) {
    case 'top':
      return textY + offset;
    case 'middle':
      return textY - iconSize / 2;
    default:
      return textY - offset - iconSize;
  }
};

//  text  icon 
export const getHorizontalTextIconPosition = (options: {
  bbox: SimpleBBox;
  textWidth: number;
  textAlign: TextAlign;
  groupedIcons: GroupedIcons;
  iconStyle: IconTheme;
  isCustomRenderer?: boolean;
}) => {
  const {
    bbox,
    textWidth,
    textAlign,
    groupedIcons,
    iconStyle,
    isCustomRenderer = false,
  } = options;
  const { x, width } = bbox;

  const leftIconWidth = getIconTotalWidth(groupedIcons.left, iconStyle);
  const rightIconWidth = getIconTotalWidth(groupedIcons.right, iconStyle);

  let textX: number;
  let leftIconX: number;
  let rightIconX: number;

  switch (textAlign) {
    case 'left':
      leftIconX = x;
      textX = x + leftIconWidth;
      rightIconX =
        textX + textWidth + (rightIconWidth && iconStyle.margin!.left!);

      break;
    case 'right':
      textX = x + width - rightIconWidth - (isCustomRenderer ? textWidth : 0);
      leftIconX = textX - (isCustomRenderer ? 0 : textWidth) - leftIconWidth;
      rightIconX =
        x +
        width -
        rightIconWidth +
        (rightIconWidth && iconStyle.margin!.left!);
      break;

    default:
      const totalWidth = leftIconWidth + textWidth + rightIconWidth;

      leftIconX = x + width / 2 - totalWidth / 2;
      textX =
        leftIconX + leftIconWidth + (isCustomRenderer ? 0 : textWidth / 2);
      rightIconX =
        leftIconX +
        leftIconWidth +
        textWidth +
        (rightIconWidth && iconStyle.margin!.left!);
      break;
  }

  return {
    textX,
    leftIconX,
    rightIconX,
  };
};

/**
 *  background-clip : https://developer.mozilla.org/en-US/docs/Web/CSS/background-clip
 * 
 * borderBox:  cell 
 * paddingBox: cell  border 
 * contentBox: cell  (border + padding) 
 * -------------------------------
 * |b|           padding         |
 * |o|  |---------------------|  |
 * |r|  |                     |  |
 * |d|  |                     |  |
 * |e|  |---------------------|  |
 * |r|           padding         |
 * -------------------------------
 * -------border-bottom-----------
 * -------------------------------
 */
export const getCellBoxByType = (
  bbox: SimpleBBox,
  borderPositions: CellBorderPosition[],
  cellStyle: CellTheme,
  boxType: CellClipBox,
): SimpleBBox => {
  if (boxType === CellClipBox.BORDER_BOX) {
    return bbox;
  }

  let { x, y, width, height } = bbox;
  const {
    padding,
    horizontalBorderWidth = 0,
    verticalBorderWidth = 0,
  } = cellStyle;

  borderPositions.forEach((position) => {
    const borderWidth = [
      CellBorderPosition.BOTTOM,
      CellBorderPosition.TOP,
    ].includes(position)
      ? horizontalBorderWidth
      : verticalBorderWidth;

    switch (position) {
      case CellBorderPosition.TOP:
        y += borderWidth;
        height -= borderWidth;
        break;
      case CellBorderPosition.BOTTOM:
        height -= borderWidth;
        break;
      case CellBorderPosition.LEFT:
        x += borderWidth;
        width -= borderWidth;
        break;
      default:
        width -= borderWidth;
        break;
    }
  });

  if (boxType === CellClipBox.CONTENT_BOX) {
    x += padding?.left!;
    y += padding?.top!;
    width -= padding?.left! + padding?.right!;
    height -= padding?.top! + padding?.bottom!;
  }

  return {
    x,
    y,
    width,
    height,
  };
};

export const getBorderPositionAndStyle = (
  position: CellBorderPosition,
  bbox: SimpleBBox,
  style: CellTheme,
) => {
  const { x, y, width, height } = bbox || {};
  const {
    horizontalBorderWidth = 0,
    horizontalBorderColorOpacity,
    horizontalBorderColor,
    verticalBorderWidth = 0,
    verticalBorderColor,
    verticalBorderColorOpacity,
    borderDash,
  } = style || {};

  //  G 
  const lineDash: LineStyleProps['lineDash'] = isEmpty(borderDash)
    ? ''
    : borderDash;

  const borderStyle: Partial<LineStyleProps> = [
    CellBorderPosition.TOP,
    CellBorderPosition.BOTTOM,
  ].includes(position)
    ? {
        lineWidth: horizontalBorderWidth,
        stroke: horizontalBorderColor,
        strokeOpacity: horizontalBorderColorOpacity,
        lineDash,
      }
    : {
        lineWidth: verticalBorderWidth,
        stroke: verticalBorderColor,
        strokeOpacity: verticalBorderColorOpacity,
        lineDash,
      };

  let x1 = 0;
  let y1 = 0;
  let x2 = 0;
  let y2 = 0;

  // horizontal
  if (
    position === CellBorderPosition.TOP ||
    position === CellBorderPosition.BOTTOM
  ) {
    let yPosition = y;

    if (position === CellBorderPosition.TOP) {
      //  Cell  Border  https://github.com/antvis/S2/issues/426
      yPosition = y + horizontalBorderWidth / 2;
    } else {
      yPosition = y + height - horizontalBorderWidth / 2;
    }

    y1 = yPosition;
    y2 = yPosition;
    x1 = x;
    x2 = x + width;
  }

  // vertical
  if (
    position === CellBorderPosition.LEFT ||
    position === CellBorderPosition.RIGHT
  ) {
    let xPosition = x;

    if (position === CellBorderPosition.LEFT) {
      xPosition = x + verticalBorderWidth / 2;
    } else {
      xPosition = x + width - verticalBorderWidth / 2;
    }

    x1 = xPosition;
    x2 = xPosition;
    y1 = y;
    y2 = y + height;
  }

  return {
    position: {
      x1,
      x2,
      y1,
      y2,
    },
    style: borderStyle,
  };
};
